export default {
	props: {
		item: {}
	},
	data() {
		return {
			isExpand: false,
			tHeight: 0,
		}
	},
	mounted() {},
	methods: {
		expand() {
			var that = this;
			let view = uni.createSelectorQuery().in(this).select("#desc-real");

			view.boundingClientRect(data => {
				that.tHeight = data.height;
				// console.log("得到布局位置信息" + JSON.stringify(data));
			}).exec();
			this.isExpand = !this.isExpand;
		}
	}
}
